import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import MenuItem from '@material-ui/core/MenuItem';
import { Form, Formik } from 'formik';
import { useDispatch } from 'react-redux';
import * as yup from 'yup';
import TextField from '@material-ui/core/TextField';
import { countries } from 'country-data';
import { onSaveProxyData } from 'redux/actions';
import { Button } from '@material-ui/core';
import { Proxy } from 'types/models/Proxy';
import Autocomplete from '@mui/material/Autocomplete';

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    autoComplete: {
        display: 'inline-block',
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(1)
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    dense: {
        marginTop: theme.spacing(2),
    },
    menu: {
        width: 200,
    },
}));
const validationSchema = yup.object({
    country: yup
        .string()
        .required('Country required'),
    ip: yup
        .string()
        .required('IP required'),
    port: yup
        .string()
        .required('PORT required'),
});

interface ActionProps {
    proxies: Proxy[];
}

const countriesArr = Object.keys(countries).map((countryCode: string) => (
    {
        label: countries[countryCode].name ? countries[countryCode].name + '(' + countryCode + ')' : 'Default',
        id: (countries[countryCode].name ? countryCode : 'default')
    }
)).filter((country) => country.id.length === 2 && country.id !== 'UK' || country.id === 'default')

const AddProxy: React.FC<ActionProps> = ({ proxies }) => {
    const dispatch = useDispatch();
    const classes = useStyles();
    const [values, setValues] = React.useState({
        ip: '',
        port: '',
        country: '',
        countryObject: { label: '', id: '' }
    });

    const handleChange = (name: string) => (event: any) => {
        setValues({ ...values, [name]: event.target.value });
    };

    const handleChangeCountry = (value: { id: string, label: string }) => {
        setValues({ ...values, country: value.id, countryObject: value });
    }
    return (
        <>
            <Formik
                validateOnChange={true}
                initialValues={{
                    country: '',
                    ip: '',
                    port: '',
                }}
                validationSchema={validationSchema}
                onSubmit={(data, { setSubmitting }) => {
                    setSubmitting(true);
                    dispatch(onSaveProxyData({ country: data.country, port: data.port, ip: data.ip, fullname: '' }));
                    setSubmitting(false);
                }}>
                {({ isSubmitting }) => (
                    <Form noValidate autoComplete='off'>
                        <Autocomplete
                            className={classes.autoComplete}
                            disablePortal
                            id='countries'
                            options={countriesArr}
                            sx={{ width: 300 }}
                            value={values.countryObject}
                            onChange={(e, v) => {
                                v && handleChangeCountry(v)
                            }}
                            renderInput={(params: any) => <TextField {...params} label='Country' />}
                        />
                        {/* <TextField
                            id='outlined-select-country'
                            select
                            label='Select'
                            className={classes.textField}
                            value={values.country}
                            onChange={handleChange('country')}
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            helperText='Please select proxy country'
                            margin='normal'
                            name='country'
                            variant='outlined'>
                            <MenuItem key='default' value='default'>Default</MenuItem>
                            {Object.keys(countries).map((countryCode: string) => {
                                return countryCode === 'all' || countryCode === '' || countryCode.length === 3 ? null : (
                                    <MenuItem key={countryCode} value={countryCode}>
                                        {countries[countryCode].name}
                                    </MenuItem>
                                )
                            })}
                        </TextField> */}
                        <TextField
                            id='outlined-ip'
                            label='IP ADDRESS'
                            defaultValue=''
                            name='ip'
                            className={classes.textField}
                            value={values.ip}
                            onChange={handleChange('ip')}
                            margin='normal'
                            variant='outlined'
                        />
                        <TextField
                            id='outlined-port'
                            label='PORT'
                            defaultValue=''
                            name='port'
                            className={classes.textField}
                            value={values.port}
                            onChange={handleChange('port')}
                            margin='normal'
                            variant='outlined'
                        />
                        <Button
                            variant='contained'
                            color='secondary'
                            type='submit'
                            onClick={() => {
                                const fullname = countries[values.country] ? countries[values.country].name : 'Unknown';
                                let saveData = {
                                    country: values.country,
                                    ip: values.ip,
                                    port: values.port,
                                    fullname: fullname
                                }
                                dispatch(onSaveProxyData(saveData))
                                setValues({
                                    country: '',
                                    ip: '',
                                    port: '',
                                    countryObject: { label: '', id: '' }
                                });
                            }}
                            // disabled={isSubmitting}
                            style={{ marginTop: 20 }}
                        >
                            SAVE
                        </Button>
                    </Form>
                )}
            </Formik>
        </>
    );
}

export default AddProxy;