import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import { Form, Formik } from 'formik';
import { useDispatch } from 'react-redux';
import * as yup from 'yup';
import TextField from '@material-ui/core/TextField';
import { countries } from 'country-data';
import { onEditProxy, onSaveProxyData } from 'redux/actions';
import { Button } from '@material-ui/core';
import { Proxy } from 'types/models/Proxy';

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    dense: {
        marginTop: theme.spacing(2),
    },
    menu: {
        width: 200,
    },
}));
const validationSchema = yup.object({
    country: yup
        .string()
        .required('Country required'),
    ip: yup
        .string()
        .required('Country required'),
    port: yup
        .string()
        .required('Country required'),
});

interface ActionProps {
    proxies: Proxy[];
    id: string;
}

const EditProxy: React.FC<ActionProps> = ({ proxies, id }) => {
    const dispatch = useDispatch();
    const classes = useStyles();
    const proxy = proxies.find((p: Proxy) => {
        if (p.country === id) {
            return p;
        }
        return false;
    })
    const [values, setValues] = React.useState({
        ip: proxy?.ip,
        port: proxy?.port,
        country: proxy?.country,
    });
    const handleChange = (name: string) => (event: any) => {
        setValues({ ...values, [name]: event.target.value });
    };
    return (
        <>
            <Formik
                validateOnChange={true}
                initialValues={{
                    country: proxy?.country,
                    ip: proxy?.ip,
                    port: proxy?.port,
                }}
                validationSchema={validationSchema}
                onSubmit={(data, { setSubmitting }) => {
                    console.log(data)
                    setSubmitting(true);
                    dispatch(onSaveProxyData({ country: data.country || '', port: data.port || '', ip: data.ip || '', fullname: '' }));
                    setSubmitting(false);
                }}>
                {({ isSubmitting }) => (
                    <Form noValidate autoComplete='off'>
                        <TextField
                            id='outlined-select-country'
                            select
                            label='Select'
                            className={classes.textField}
                            value={values.country}
                            onChange={handleChange('country')}
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            helperText='Please select proxy country'
                            margin='normal'
                            name='country'
                            variant='outlined'>
                            {Object.keys(countries).map((countryCode: string) => (
                                <MenuItem key={countryCode} value={countryCode}>
                                    {countries[countryCode].name}
                                </MenuItem>
                            ))}
                        </TextField>
                        <TextField
                            id='outlined-ip'
                            label='IP ADDRESS'
                            defaultValue=''
                            name='ip'
                            className={classes.textField}
                            value={values.ip}
                            onChange={handleChange('ip')}
                            margin='normal'
                            variant='outlined'
                        />
                        <TextField
                            id='outlined-port'
                            label='PORT'
                            defaultValue=''
                            name='port'
                            className={classes.textField}
                            value={values.port}
                            onChange={handleChange('port')}
                            margin='normal'
                            variant='outlined'
                        />
                        <Button
                            variant='contained'
                            color='primary'
                            type='submit'
                            onClick={() => {
                                let saveData = {
                                    country: values.country || '',
                                    ip: values.ip || '',
                                    port: values.port || '',
                                    fullname: countries[values.country || ''].name
                                }
                                dispatch(onSaveProxyData(saveData))
                                dispatch(onEditProxy(false, ''))
                            }}
                            // disabled={isSubmitting}
                            style={{ marginTop: 20 }}
                        >
                            UPDATE
                        </Button>
                        <Button
                            variant='contained'
                            color='secondary'
                            type='button'
                            onClick={() => {
                                dispatch(onEditProxy(false, ''))
                            }}
                            // disabled={isSubmitting}
                            style={{ marginTop: 20, marginLeft: 10 }}
                        >
                            CANCEL
                        </Button>
                    </Form>
                )}
            </Formik>
        </>
    );
}

export default EditProxy;