import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import clsx from 'clsx';
import Box from '@material-ui/core/Box';
import useStyles from './TableItem.style';
import TableActions from './TableActions';
import { Proxy } from '../../../types/models/Proxy';

interface TableItemProps {
  data: Proxy;
  index: number;
}

const TableItem: React.FC<TableItemProps> = ({ data, index }) => {
  const classes = useStyles();
  const getStatusColor = () => {
    switch (data.status) {
      case 'running': {
        return '#F84E4E';
      }
      case 'stopped': {
        return '#43C888';
      }
      default: {
        return '#E2A72E';
      }
    }
  };

  return (
    <TableRow key={data.id} className='item-hover'>
      <TableCell component='th' scope='row' className={classes.tableCell}>
        <Box className={classes.anchar}>{index}</Box>
      </TableCell>
      <TableCell
        align='left'
        className={clsx(classes.tableCell, classes.tableCell)}>
        {data.fullname}
      </TableCell>
      <TableCell
        align='left'
        className={clsx(classes.tableCell, classes.tableCell)}>
        {data.ip}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        {data.port}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        <Box
          className={classes.badgeRoot}
          style={{
            color: getStatusColor(),
            backgroundColor: getStatusColor() + '44',
          }}>
          {data.status === 'running' ? 'Running' : data.status === 'stopped' ? 'Stopped' : ''}
        </Box>
      </TableCell>
      <TableCell align='right' className={classes.tableCell}>
        <TableActions id={data.id} />
      </TableCell>
    </TableRow>
  );
};

export default TableItem;
