import React from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Fade from '@material-ui/core/Fade';
import IconButton from '@material-ui/core/IconButton';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Box from '@material-ui/core/Box';
import { onStartProxy, onStopProxy, onDeleteProxy, onEditProxy } from 'redux/actions';
import { useDispatch } from 'react-redux';
import { Snackbar, SnackbarContent } from '@material-ui/core';

interface TableActionProps {
  id: string;
}

const TableActions: React.FC<TableActionProps> = ({ id }) => {
  const [openCopiedSnackbar, setOpenCopiedSnackbar] = React.useState(false);
  const [message, setMessage] = React.useState('');
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  const dispatch = useDispatch();

  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleStopProxy = () => {
    dispatch(onStopProxy(id));
    setMessage('Stopped')
    setAnchorEl(null);
  };

  const handleStartProxy = () => {
    dispatch(onStartProxy(id));
    setMessage('Started')
    setAnchorEl(null);
  };

  const handleDeleteProxy = () => {
    dispatch(onDeleteProxy(id));
    setMessage('Deleted')
    setAnchorEl(null);
  };

  const handleEditProxy = () => {
    dispatch(onEditProxy(true, id));
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  }

  const onCloseSnackbarHandler = (_event: any, _reason: any) => {
    setOpenCopiedSnackbar(false);
  }

  return (
    <Box>
      <IconButton
        aria-controls='fade-menu'
        aria-haspopup='true'
        onClick={handleClick}>
        <MoreVertIcon />
      </IconButton>
      <Menu
        id='fade-menu'
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}>
        <MenuItem style={{ fontSize: 14 }} onClick={handleEditProxy}>
          Edit Proxy
        </MenuItem>
        <MenuItem style={{ fontSize: 14 }} onClick={handleStartProxy}>
          Start Proxy
        </MenuItem>
        <MenuItem style={{ fontSize: 14 }} onClick={handleStopProxy}>
          Stop Proxy
        </MenuItem>
        <MenuItem style={{ fontSize: 14 }} onClick={handleDeleteProxy}>
          Delete Proxy
        </MenuItem>
      </Menu>
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={openCopiedSnackbar}
        autoHideDuration={6000}
        onClose={onCloseSnackbarHandler}>
        <SnackbarContent
          className='success'
          aria-describedby='client-snackbar'
          message={
            <span id='client-snackbar'>
              {message}
            </span>
          }
        />
      </Snackbar>
    </Box>
  );
};
export default TableActions;
